import React, { Component } from 'react'
import './css.css';
import Web3 from 'web3';
import ShopToken from '../ShopToken/ShopToken'
import FreeToken from '../FreeToken/FreeToken'
import ShopPair from '../ShopPair/ShopPair'
import ShopHeader from '../ShopHeader/ShopHeader'

class Favorites extends Component {
constructor(){
  super()
  this.state={show:"products",favoriteProducts:[],favoriteShops:[]}
  this.reload = this.reload.bind(this);
}

    componentDidMount(){
        if(this.state.show=="products")this.loadFaves()
        if(this.state.show=="shops")this.loadShops()
    }

    reload(){
      if(this.state.show=="products")this.loadFaves()
      if(this.state.show=="shops")this.loadShops()
    }

async loadFaves(){
    
        
    
    let final=[]
    let s=this.getCookie("favorite")

    let arr=s.split(",")
    let prp=this.props
    let stt=this.state
    await window.ethereum.enable().then(async function (accounts) { 
     
    for(let i=0;i<arr.length;i++){
      
      if(arr[i]!=""){
        
        let token=arr[i].substr(0,arr[i].indexOf("_"))
        
        let list=arr[i].substr(arr[i].indexOf("_")+1,42)
        if(token!="undefined"){
          if(list!="undefined"){
        let index=arr[i].substr(arr[i].lastIndexOf("x")+1,arr[i].length)
        const { REACT_APP_TICKETABI } = process.env;
        
        const tickC = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), list)
         
        let priceList = await tickC.methods.prices().call()
        
        let code = await tickC.methods.code().call()
        
        const { REACT_APP_PriceListABI } = process.env;
        const priceListC = new web3.eth.Contract(JSON.parse(REACT_APP_PriceListABI), priceList) 
        let price = await priceListC.methods.price(token).call()
        
        if(code==4)final.push(<ShopToken db={prp.db} priceList={list} address={token} price={price} index={list+"x"+index} ticket={list} key={i} edit={""} name={"nnn"}></ShopToken>);
        if(code==7)final.push(<FreeToken   db={prp.db} priceList={list} address={token} price={price} index={list+"x"+index} ticket={list} key={i} edit={""} name={"nnn"}></FreeToken>);
        if(code==9)final.push(<ShopPair  db={prp.db} priceList={list} address={token} price={price} index={list+"x"+index} ticket={list} key={i} edit={""} name={"nnn"}></ShopPair>);
          }}
      }
    }

  })
  this.setState({favoriteProducts:final})
}

async loadShops(){
  let final=[]
  let s=this.getCookie("favoriteShop")

  let arr=s.split(",")
  let rel=() => this.reload()
  await window.ethereum.enable().then(async function (accounts) { 
   
  for(let i=0;i<arr.length;i++){
    
    if(arr[i]!=""){
      
      let ticket=arr[i].substr(0,arr[i].indexOf("_"))
      if(ticket!="undefined"){
      const { REACT_APP_TICKETABI } = process.env;    
      const tickC = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), ticket)
      
      let code = await tickC.methods.code().call()
      
      final.push(<ShopHeader  reload={rel} ticket={ticket} key={i} index={i}></ShopHeader>);
      }
    }

  }

})
this.setState({favoriteShops:final})
}

    deleteFavorites(what){
       if(what=="shops"){
        document.cookie = "favoriteShop=aaa; expires=Fri, 27 Jul 2001 02:47:11 UTC; path=/";
        document.cookie = "favoriteShop=aaa; expires=Fri, 27 Jul 2001 02:47:11 UTC; path=/smartshop/";  
        document.cookie = "favoriteShop=aaa; expires=Fri, 27 Jul 2001 02:47:11 UTC;"; 
       }
       if(what=="products"){
        document.cookie = "favorite=aaa; expires=Fri, 27 Jul 2001 02:47:11 UTC; path=/"; 
        document.cookie = "favorite=aaa; expires=Fri, 27 Jul 2001 02:47:11 UTC; path=/smartshop/"; 
        document.cookie = "favorite=aaa; expires=Fri, 27 Jul 2001 02:47:11 UTC;"; 
        }
        this.loadFaves()
        this.loadShops()
   
    }
   
    
    getCookie(cname) {
        var name = cname + "=";
     
        var decodedCookie = decodeURIComponent(document.cookie);
        var ca = decodedCookie.split(';');
        for(var i = 0; i <ca.length; i++) {
          var c = ca[i];
          while (c.charAt(0) == ' ') {
            c = c.substring(1);
          }
          if (c.indexOf(name) == 0) {
            return c.substring(name.length, c.length);
          }
        }
        return "";
      }

      toggle(what){
       this.setState({show:what})
      if(what=="products")this.loadFaves()
      if(what=="shops")this.loadShops()
      }
   
    render(){
        console.log(this.props.update) 
        return ( 
        <div>

        <div id="faves_flyer"><div id="favorites_mainlabel">favorites</div>
            <div id="favorites_image"  alt="favorites">⭐️</div>
            <div id="favorites_sublabel">favorite products list</div>
            <div id="faves_menu_panel">
              <div id="faves_menu_shops" className={"faves_menu_icon "+((this.state.show=="shops")?"selected":"")} onClick={() => this.toggle("shops")}>❤️</div> 
              <div id="faves_menu_products" className={"faves_menu_icon "+((this.state.show=="products")?"selected":"")} onClick={() => this.toggle("products")}>⭐️</div> 
            </div> 
      <div id="faves_panel"></div>
      {(this.state.show=="products")?
        <div className="faves_panel">{this.state.favoriteProducts}</div>
        :<></>}
        {(this.state.show=="shops")?
        <div className="faves_panel">{this.state.favoriteShops}</div>
        :<></>}
        <input className="delete_button" type="button" value="✂️❤️" onClick={() => this.deleteFavorites("shops")}></input>
      <input className="delete_button" type="button" value="✂️⭐️" onClick={() => this.deleteFavorites("products")}></input>
      

      </div>
        </div>
        );
    }


}


const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")

export default Favorites