import React, { Component } from 'react'
import Web3 from 'web3';
import ShopToken from '../ShopToken/ShopToken'


import './css.css';


class PRiceList1_0 extends Component {
 constructor(props){
     super()
     this.state={
         edit:true,
         items:[],
         box:"",
         boxindex:0,
         ticket:"",
         focus:"",
         loading:true
        }
 }

    componentDidMount() {
        this.loadItems()
        
    }




    async setPrice(){
        let addr=this.props.address
        await window.ethereum.enable().then(await async function (accounts) { 
            const { REACT_APP_TICKETABI } = process.env;
            const { REACT_APP_PriceListABI } = process.env;
            const priceListC = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), addr) 
                let priceListz = await priceListC.methods.prices().call()
                //console.log("priceListz",priceListz)
                const prices = new web3.eth.Contract(JSON.parse(REACT_APP_PriceListABI), priceListz) 
            if(document.getElementById("Shop_add_item_setprice_field").value!=""){
            if(parseFloat(document.getElementById("Shop_add_item_setprice_field").value)>=0.001){
            await prices.methods.setPrice(document.getElementById("Shop_add_item_setaddress_field").value,parseInt(parseFloat(document.getElementById("Shop_add_item_setprice_field").value)*1000)).send({from: accounts[0] , gas: 323456 })
            }else{
                alert("SET A PRICE > 0.001")
            }    
        }else{
                alert("SET A TOKEN ADDRESS IN ORDER TO ADD A TOKEN TO THE SHOP!")
            }
         
        })
        this.loadItems()
        
    }



    async loadItems() {
      
        let total_listing=[]

        let addr=this.props.address
        let priceListz
        let box
        await window.ethereum.enable().then(async function (accounts) {
            let account=accounts[0];
            try{
                const { REACT_APP_TICKETABI } = process.env;
                const { REACT_APP_PriceListABI } = process.env;
            const tick = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), addr) 
            priceListz = await tick.methods.prices().call()
            //console.log("priceListz",priceListz)
            box = await tick.methods.box().call()
            console.log("BOX "+box+" LIST "+priceListz+" TICKET "+addr)
            const prices = new web3.eth.Contract(JSON.parse(REACT_APP_PriceListABI), priceListz) 
            let index = await prices.methods.priceListing(0).call()
            console.log("index",index)
            for(let i=0;i<parseInt(index[2]);i++){
                
                //if(((getUrlVars()["index"]!="")&&(i==getUrlVars()[index]))){
                    if(((getUrlVars()["index"]!="")&&(i==getUrlVars()["index"]))||(!getUrlVars()["index"])){
                     
                let item = await prices.methods.priceListing(i).call()
                try{
                    const { REACT_APP_ABI } = process.env;
                    const tkn = new web3.eth.Contract(JSON.parse(REACT_APP_ABI), item[0])                
                    let name = await tkn.methods.totalSupply().call()
                    let a=[]
                    a.push(item)
                    a.push(name)
                    total_listing.push(a)
                }catch(e){}  
                }
            }
            console.log("LOADED ITEMS "+total_listing)
            
        }catch(e){console.log("PriceList loadItems()",e)}
        })

        this.setState({items : total_listing,priceList : priceListz,focus : addr,box : box,loading:false})
        try{this.props.setAmount(total_listing.length)}catch(e){}
        //this.render()
        
            
        }

        async checkAddress(){
            await window.ethereum.enable().then(async function (accounts) {
                let account=accounts[0];
                try{
                const { REACT_APP_ABI } = process.env;
                let addr=document.getElementById("Shop_add_item_setaddress_field").value
                const tick = new web3.eth.Contract(JSON.parse(REACT_APP_ABI), addr) 
                let symbol = await tick.methods.symbol().call()
                document.getElementById("address_to_sell").innerHTML=symbol
                document.getElementById("address_to_sell").style.color='white'
                }catch(e){
                    document.getElementById("address_to_sell").style.color='red'
                    document.getElementById("address_to_sell").innerHTML="address"
                }
                })

        }


         editMode(){
           // alert("STATE: "+this.state.edit)
            (!this.state.edit)?this.setState({edit:true}):this.setState({edit:false})
        }

    render(){
const final = [];
let n=0;
console.log("PRICELIST STATE",this.state)
if(this.props.address!=this.state.focus)this.loadItems()
  for (let  itm of this.state.items) { 
    final.push(<ShopToken db={this.props.db} priceList={this.state.priceList} address={itm[0][0]} price={itm[0][1]} index={this.props.address+"x"+n} ticket={this.props.address} key={n} edit={this.props.edit} name={itm[1]}></ShopToken>);
    n++
}
        return ( 
        
            
                
                <div className="Shop_module_main">
                  {
                (this.state.loading)?<center><img className="loadergif" id="pricelist_loadergif" src="https://androidexample365.com/content/images/2019/07/rainbow_ring.gif"></img></center>:<></>
                }
                {(final.length>0)?
                <div>
                
                <div className="Shop_module_item_row">
                    <div className="Shop_module_link_panel">
                    {(!getUrlVars()["shop"])?<span className="Price_list_label" onClick={() => window.open("https://ropsten.etherscan.io/address/"+this.state.focus)}>🎟️ #{this.props.index}</span>:<span className="Price_list_label"><div  className="ticket_link" onClick={() => window.open("https://ropsten.etherscan.io/address/"+this.state.focus)}>🎟️</div></span>}
                    {(!getUrlVars()["shop"])?<span className="PriceList_module_link_icon right" onClick={() => jump(this.state.focus)}>🛒</span>:<></>}
                        <div className="box_link_holder">
                            <span className="PriceList_module_link_icon right" onClick={() => jumpBox(this.state.box)}>📦</span>
                            <span className="PriceList_module_link_icon right" onClick={() => copyBoxAddress(this.state.box)}>📝</span>
                        </div> 
                     </div>       
                </div>
                {final}
                </div>
                :
                <>
                {(this.props.edit)?<div className="empty_message">#{this.props.index} sell something!</div>:<></>}
                {(this.props.edit)?
                <div className="Shop_add_item_row">
                    <div className="Shop_module_link_panel">
                    {(!getUrlVars()["shop"])?<span className="Price_list_label" onClick={() => window.open("https://ropsten.etherscan.io/address/"+this.state.focus)}>🎟️ #{this.props.index}</span>:<span className="Price_list_label"><div className="ticket_link" onClick={() => window.open("https://ropsten.etherscan.io/address/"+this.state.focus)}>🎟️</div></span>}
                    {(!getUrlVars()["shop"])?<span className="PriceList_module_link_icon right" onClick={() => jump(this.state.focus)}>🛒</span>:<></>}
                    <div className="box_link_holder">
                            <span className="PriceList_module_link_icon right" onClick={() => jumpBox(this.state.box)}>📦</span>
                            <span className="PriceList_module_link_icon right" onClick={() => copyBoxAddress(this.state.box)}>📝</span>
                        </div> 
                    </div>       
                </div>:
                <></>}
                </>
                }
                {(this.props.edit)?
                <div>
                <div className="Shop_add_item_row">
                    <div className="Shop_add_item_panel">
                            <img id="Shop_add_item_icon" src="https://i.imgur.com/YKhumij.png"></img>
                            <div className="Shop_item_setprice">
                                <input id="Shop_add_item_setaddress_field" className="Shop_item_setprice_field" type="text" onKeyUp={() => this.checkAddress()} onKeyPress={() => this.checkAddress()}></input>
                                <span id="address_to_sell" className="right paddingright">address</span>
                            </div>
                            <div className="Shop_item_setprice">
                                <input id="Shop_add_item_setprice_field" className="Shop_add_item_setprice_field" type="text"></input>
                                <input className="Shop_item_setprice_button" type="button" value="✒️ Set Price" onClick={() => this.setPrice()}></input>
                            </div>
                    </div>
                </div>
                </div>:
                    <div></div>
                    }
                  
        </div>
        );
    }


}

function copyBoxAddress(addr){
    var copyText = document.createElement('textarea')
    copyText.value = addr;
    document.getElementById("home").appendChild(copyText)
    copyText.focus();
    copyText.select();
    copyText.setSelectionRange(0, 99999) /*For mobile devices*/
    document.execCommand("copy")
    document.getElementById("home").removeChild(copyText)
}

function jumpBox(addr){
    window.open('https://ropsten.etherscan.io/address/'+addr)
}


function jump(addr){
    window.open('/smartshop/?shop='+addr)
}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}


const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")

export default PRiceList1_0