import React, { Component } from 'react'
import Web3 from 'web3'
import './css.css';



class ShopToken extends Component {
constructor(props){
    super()
    this.state={favorite:false,balance:0}

}


setCookie(cname, cvalue,id) {
 
    let s=this.getCookie(cname)
    let arr=s.split(",")
    let arr2=[]
    let t=false
    for(let i=0;i<arr.length;i++){
        if(arr[i]==cvalue+"_"+id){
            t=true
          
        }else{
            arr2.push(arr[i])
        }
    }
  
    if(!t){
        arr2.push(cvalue+"_"+id)
       
    }
    document.cookie = cname + "=" + arr2.toString() + "; path=/"
    

    this.loadToken()
}

getCookie(cname) {
    var name = cname + "=";
    var decodedCookie = decodeURIComponent(document.cookie);
    var ca = decodedCookie.split(';');
    for(var i = 0; i <ca.length; i++) {
      var c = ca[i];
      while (c.charAt(0) == ' ') {
        c = c.substring(1);
      }
      if (c.indexOf(name) == 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }


async buy(){
    let addr=this.props.address
        let index=this.props.index
        let ticket=this.props.ticket
    let max
    document.getElementById('cost_'+this.props.index).style.color="white"
    await window.ethereum.enable().then(async function (accounts) { 
    await web3.eth.getBalance(accounts[0],async  function(err, result) {
        if (err) {
            console.log(err)
        } else {
            max=await result
        }
    })
    
    })
    let spend=await web3.utils.toWei(""+(document.getElementById("cost_"+index).value*1000), 'finney')
    if(spend>=max){
        alert("If you spend all your ETH you won't have enough to pay for tyhe gas!")
    }else{
        
        await window.ethereum.enable().then(async function (accounts) { 
            const { REACT_APP_TICKETABI } = process.env;
            const { REACT_APP_TICKET } = process.env;
            const door = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), ticket) 
            await door.methods.buy(addr,"0x54d5aC00babbF6884336D313d7Afe08279015DD4").send({from: accounts[0] , gasLimit: 223456 , value: web3.utils.toWei(""+(document.getElementById("cost_"+index).value*1000), 'finney')})
    })  
    this.loadToken()
}
}

async setPrice(){
    let addr=this.props.address
    let index=this.props.index
    let priceList=this.props.priceList
    if(document.getElementById("Shop_item_setprice_field_"+index).value*1000>=1){
    await window.ethereum.enable().then(async function (accounts) { 
        const { REACT_APP_PriceListABI } = process.env;
        
        const prices = new web3.eth.Contract(JSON.parse(REACT_APP_PriceListABI), priceList) 
        await prices.methods.setPrice(addr,document.getElementById("Shop_item_setprice_field_"+index).value*1000).send({from: accounts[0] , gas: 123456 })
        document.getElementById('Shop_item_price_'+index).innerHTML=document.getElementById("Shop_item_setprice_field_"+index).value
    }) 
    this.loadToken() 
}else{
    alert("PRICE MUST BE >= 0.001")
}
}

async calculate_price(){
    let amount=document.getElementById('Shop_item_setamount_field_'+this.props.index).value
    if(amount>this.state.balance){
    document.getElementById('Shop_item_setamount_field_'+this.props.index).value=this.state.balance
    amount=this.state.balance
    }
    let price=parseFloat(1000000*amount)*(parseFloat(this.props.price)/1000)/1000000
    document.getElementById("cost_"+this.props.index).style="text-align:right;"
    document.getElementById('cost_'+this.props.index).value=price
    let max
    await window.ethereum.enable().then(async function (accounts) { 
        await web3.eth.getBalance(accounts[0],async  function(err, result) {
            if (err) {
                console.log(err)
            } else {
                max=await web3.utils.fromWei(result, "ether")
            }
        })
        })
        document.getElementById('cost_'+this.props.index).style.color="white"
        document.getElementById("Shop_item_button_"+this.props.index).style.color="white"
        if((price>max)||(price==0)){
        document.getElementById('cost_'+this.props.index).style.color="red"
        document.getElementById("Shop_item_button_"+this.props.index).style.color="red"
        }
}

calculate_backprice(){
    let cost=document.getElementById('cost_'+this.props.index).value
    let amount=parseFloat(1000000*cost)/(parseFloat(this.props.price)/1000)/1000000

    //document.getElementById("Shop_item_setamount_field_"+this.props.index).style="text-align:right;"
    document.getElementById('Shop_item_setamount_field_'+this.props.index).value=amount
    document.getElementById('Shop_item_setamount_field_'+this.props.index).style.color="white"
    document.getElementById("Shop_item_button_"+this.props.index).style.color="white"
    if(this.state.balance<amount){
    document.getElementById('Shop_item_setamount_field_'+this.props.index).style.color="red"
    document.getElementById("Shop_item_button_"+this.props.index).style.color="red"
    }
}

setMax(){
    document.getElementById('Shop_item_setamount_field_'+this.props.index).style.color="white"
    document.getElementById('Shop_item_setamount_field_'+this.props.index).value=this.state.balance
this.calculate_price()
}

async setETHMax(){
    let max
    document.getElementById('cost_'+this.props.index).style.color="white"
    await window.ethereum.enable().then(async function (accounts) { 
    await web3.eth.getBalance(accounts[0],async  function(err, result) {
        if (err) {
            console.log(err)
        } else {
            max=await web3.utils.fromWei(result, "ether")
        }
    })
    
    })
    
    document.getElementById('cost_'+this.props.index).value=max
    this.calculate_backprice()
}

check_price(){
    document.getElementById("Shop_item_setprice_button"+this.props.index).style.color="white"
    if(parseFloat(document.getElementById("Shop_item_setprice_field_"+this.props.index).value)<0.001){
    document.getElementById("Shop_item_setprice_button"+this.props.index).style.color="red"
    if(parseFloat(document.getElementById("Shop_item_setprice_field_"+this.props.index).value)>0){
    document.getElementById("Shop_item_setprice_field_"+this.props.index).value="0.001"
    document.getElementById("Shop_item_setprice_button"+this.props.index).style.color="white"
}}
}

async loadToken(){
    let addr=this.props.address
    let index=this.props.index
    let ticket=this.props.ticket
    let db=this.props.db
    let bal

    let s=this.getCookie("favorite")
    let arr=s.split(",")
    let t=false
    document.getElementById("favorite_"+index).style.opacity=0.3
    for(let i=0;i<arr.length;i++){
        if(arr[i]==addr+"_"+index)
            document.getElementById("favorite_"+index).style.opacity=1
    }
    await window.ethereum.enable().then(async function (accounts) { 
        try{ 
            const { REACT_APP_ABI } = process.env; 
            const { REACT_APP_TICKETABI } = process.env; 
        const tick = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), ticket) 
        const tkn = new web3.eth.Contract(JSON.parse(REACT_APP_ABI), addr) 
        let name = await tkn.methods.name().call()
        let box = await tick.methods.box().call()
        document.getElementById("Shop_item_"+index).innerHTML=name
        let dec = await tkn.methods.decimals().call()
        let symbol = await tkn.methods.symbol().call()

    
        bal = await tkn.methods.balanceOf(box).call()/Math.pow(10,dec)
        
        document.getElementById("Shop_item_setamount_field_"+index).value=bal
        document.getElementById("cost_"+index).style="text-align:left;"
        document.getElementById("cost_"+index).value=symbol
        }catch(e){console.log("SHOP TOKEN ERROR "+db+e)}
})
this.setState({balance:bal})
this.render()
}

    componentDidMount(){
        this.loadToken()
      
    }

    render(props){
    console.log("SHOP TOKEN PROPS>",this.props)

    
        return ( 
            <div className="row_container">
            <div className="product_tool">
                <div id={"favorite_"+this.props.index} className={(this.state.favorite)?"favorite":"notfavorite"} onClick={() => this.setCookie("favorite",this.props.address,this.props.index)}>⭐️</div>
                <div id={"favorite_"+this.props.index} className={(this.state.favorite)?"favorite2":"notfavorite2"} onClick={() => openProduct(this.props.ticket,this.props.index)}>💊</div>
            </div>
        <div className="Shop_row">        

            <div id={"Shop_Token_item_panel_"+this.props.index} className={(this.state.balance==0)?"Shop_Token_item_panel_disabled":"Shop_Token_item_panel"}>
               <img className="Shop_item_icon" onClick={() => window.open('https://ropsten.etherscan.io/address/'+this.props.address)} src="https://dapp.dfohub.com/assets/img/default-logo.png"></img>
               <div id={"Shop_item_"+this.props.index} className="Shop_item_label">loading...</div>
               <img className="Shop_item_price_icon" src="https://i.imgur.com/FPL7c5a.png"></img>
               <span id={"Shop_item_price_"+this.props.index} className="Shop_item_price right">{(this.props.price)/1000}</span>
               <div className="clear"></div>
                {(this.props.edit)?
                <div className="Shop_item_setprice">
                    <input id={"Shop_item_setprice_field_"+this.props.index} className="Shop_item_setprice_field" type="text"onKeyUp={() => this.check_price()} onKeyPress={() => this.check_price()}></input>
                    <input id={"Shop_item_setprice_button"+this.props.index} className="Shop_item_setprice_button" type="button" value="✒️ Set Price" onClick={() => this.setPrice()}></input>
                </div>
                :
               <div className="Shop_item_setprice">
                   <input id={"Shop_item_setamount_field_"+this.props.index} className="Shop_item_setamount_field" type="text" onKeyUp={() => this.calculate_price()} onKeyPress={() => this.calculate_price()}></input>
                   <img className="Shop_item_icon"  onClick={() => this.setMax()} src="https://dapp.dfohub.com/assets/img/default-logo.png"></img>
                   <input  id={"cost_"+this.props.index} className="Shop_item_setamount_field" type="text"  onKeyUp={() => this.calculate_backprice()} onKeyPress={() => this.calculate_backprice()}></input>
                   <img className="Shop_item_icon"  onClick={() => this.setETHMax()} src="https://i.imgur.com/FPL7c5a.png"></img>
                   <input id={"Shop_item_button_"+this.props.index} className="Shop_item_setprice_button" type="button" value="🛒 buy!" onClick={() => this.buy()}></input>
               </div>
    }
                
               </div>
        </div>
        </div>   
        );
    }


}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

function openProduct(ticket,index){
    
window.open("/smartshop/?shop="+ticket+"&index="+index.substr(index.lastIndexOf("x")+1,10))
}

        
const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")




export default ShopToken