import React, { Component } from 'react'
import Web3 from 'web3';
import ShopToken from '../ShopToken/ShopToken'
import ShopModulesPanel from '../ShopModulesPanel/ShopModulesPanel'
import BoxModules from '../BoxModules/BoxModules'
import SingleShopMenu from '../SingleShopMenu/SingleShopMenu'
import ShopMenu from '../ShopMenu/ShopMenu'
import ShopMeta from '../ShopMeta/ShopMeta'
import SingleUserBook from '../SingleUserBook/SingleUserBook'
import PRiceList1_0 from '../PRiceList1_0/PRiceList1_0'
import FreebiesList1_0 from '../FreebiesList1_0/FreebiesList1_0'
import PairPriceList1_0 from '../PairPriceList/PairPriceList1_0';
import ipfs from './../../ipfs'
import utils from './../../library'
import Base64Binary from './../../base64'


import './css.css';


class Prices extends Component {
 constructor(){
     super()
     this.state={
         edit:true,
         items:[],
         box:"",
         boxindex:0,
         ticket:"",
         ticketindex:0,
         freebies:"",
         freebiesindex:0,
         swapper:"",
         swapperindex:0,
         mode:"default",
         amountItems:0,
         wallet:"",
         ismaster: false,
         sharing:false,
         sharingPage:"",
         shopName:"",
         shopDescription:""
        }
     this.showBox = this.showBox.bind(this);
     this.showTicket = this.showTicket.bind(this);
     this.showFreebie = this.showFreebie.bind(this);
     this.showSwapper = this.showSwapper.bind(this);
     this.setMode=this.setMode.bind(this)
     this.setAmountItems=this.setAmountItems.bind(this)
 }

    async showBox(addr,ind){
        this.setState({box:addr,boxindex:ind,ticket:"",ticketindex:0,freebies:"",freebiesindex:0,swapper:"",swapperindex:0})
        console.log("SHOW BOX")
        this.render()
    }

    componentDidMount(){
    if(utils.getUrlVars()["shop"]>"")this.setState({edit:false})
       this.setWallet()
       this.loadHeader()
       this.setShop()
    }

    async setShop(){
        if(utils.getUrlVars()["shop"]){
            let account
            let code
            let ismaster=false
            await window.ethereum.enable().then(async function (accounts) {
                const { REACT_APP_TICKETABI } = process.env;
                const door = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), utils.getUrlVars()["shop"]) 
                code = await door.methods.code().call()
                let box= await door.methods.box().call()
                const { REACT_APP_HAPPYBOXABI } = process.env;
                const boxC = new web3.eth.Contract(JSON.parse(REACT_APP_HAPPYBOXABI),box) 
                let master= await boxC.methods.master().call()
                if(master.toLowerCase()==accounts[0].toLowerCase())ismaster=true
            })
            this.setState({ismaster: ismaster})
            if(code==4)this.setState({ticket: utils.getUrlVars()["shop"]})
            if(code==7)this.setState({freebies: utils.getUrlVars()["shop"]})
            if(code==9)this.setState({swapper: utils.getUrlVars()["shop"]})
            //this.render()
        }
    }

    async generateSharePage(time){
        this.setState({sharing:true})
        this.savePage(time)
          }

 



    async savePage(time){
        try{
            if(time>60)
            document.getElementById("twitter_sharing_info_panel").innerHTML="ipfs problems? better try later..."
            if(time==60)
            document.getElementById("twitter_sharing_info_panel").innerHTML="Please wait while propagating twitter card on ipfs..."
        }catch(e){}
        

            let myImage = new Image(600, 330);
        let shopNam=this.state.shopName
        let shopDescriptio=this.state.shopDescription    
        let image_0=""
        let module="🎟️ Vending Machine"
            if(utils.getUrlVars()["shop"]>""){
                await window.ethereum.enable().then(async function (accounts) {
                    const { REACT_APP_TICKETABI } = process.env;
                    const door = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), utils.getUrlVars()["shop"]) 
                    let code = await door.methods.code().call()
                    if(code==4){
                        module="🎟️ Vending Machine"
                        image_0="/smartshop/ethereum400.png"
                    }
                    if(code==7){module="🎀 Freebies Distributor"
                        image_0="/smartshop/free.png"
                    }
                    if(code==9){module="💫 Swap Machine"
                        image_0="/smartshop/ethereum400.png"
                    }
                });
            }
            myImage.onload = async function(){
                console.log("image loaded")   
                let myETHImage = new Image(200, 200);
                myETHImage.onload = async function(){
                    const canvas = document.createElement('canvas');
                    const ctx = canvas.getContext('2d');
                    canvas.width = myImage.width;
                    canvas.height = myImage.height;
                    ctx.fillStyle = "red";
                    ctx.fillRect(0, 0, canvas.width, canvas.height);
                    
                    ctx.drawImage(myImage,0,0,600,330)
                    ctx.drawImage(myETHImage,400,70,150,150)
                    ctx.fillStyle = "#0788ae";
                    ctx.font = "30px Arial";           
                    ctx.fillText(module, 65, 85);
                    ctx.fillStyle = "white";
                    ctx.font = "30px Arial";
                    let amount=document.getElementById("Shop_amount").getAttribute("tot")
                    ctx.fillText("🏺 "+ amount+" items", 65, 180);
                    ctx.fillStyle = "#0788ae";
                    ctx.font = "35px Arial";
                    let tempName="Crypto Tokens Shop"
                    if(shopNam!="")tempName=shopNam
                    ctx.fillText(tempName, 70, 225);
                    ctx.fillStyle = utils.getRandomColor()
                    ctx.fillRect(canvas.width-2, canvas.height-2, 1, 1);
                    let baseSixtyFour=canvas.toDataURL().replace(/data:image\/png;base64,/,'');
                    let byteArray = Base64Binary.decodeArrayBuffer(baseSixtyFour);  
                    var enc = new TextEncoder(); // always utf-8
                    const buffer0 = await Buffer.from(byteArray);
                    console.log("IPFS0");
                    await ipfs.add(buffer0).then(async (response) => {
                        
                        let image="https://ipfs.io/ipfs/"+response["path"].toString()
                        let tempPage='<html lang="en"><head><title>SmartShop Twitter Share</title><meta name="viewport" content="width=device-width, initial-scale=1" /><meta name="twitter:card" content="summary_large_image"><meta name="twitter:site" content="@maxxflyer"><meta name="twitter:creator" content="@maxxflyer"><meta name="twitter:title" content="'+shopNam+'"><meta name="twitter:description" content="'+shopDescriptio+'"><meta name="twitter:image" content="'+image+'"><meta name="theme-color" content="#000000" /><meta name="description" content="'+shopDescriptio+'"></head><body style="background:#0788ae;"><script>window.location.href = "https://happyfarm-dfo.github.io/smartshop/?shop='+utils.getUrlVars()["shop"]+'";</script><div>'+Math.random()+'</div></body></html>'
                        alert(tempPage)
                        const buffer = await Buffer.from(tempPage);
                        console.log("IPFS1");
                        ipfs.add(buffer).then(async (response2) => { 
                            utils.countAndOpen('https://twitter.com/intent/tweet?url=https%3A%2F%2Fipfs.io%2Fipfs%2F'+response2["path"].toString(),time,"twitter_sharing_info_panel","Please wait while propagating twitter card on ipfs ...")
                        }).catch((err) => {       
                            console.error(err)   
                        })
                    }).catch((err) => {       
                        console.error(err)   
                    })
                };
                myETHImage.src = image_0
            };        
            myImage.src = '/smartshop/smartshop_twitter_base.jpg'
    }

    async setWallet(){
        if(!utils.getUrlVars()["shop"]){
            let account
            await window.ethereum.enable().then(function (accounts) {
                account=accounts[0]
                document.getElementById("Shop_social").innerHTML=utils.getAvatar(account)
            })
            this.setState({wallet:account})
            this.render()          
        }
        if(utils.getUrlVars()["shop"]>""){
            let t=utils.getUrlVars()["shop"]
            await window.ethereum.enable().then(async function (accounts) {
                const { REACT_APP_TICKETABI } = process.env;
                const tick = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), t) 
                let box = await tick.methods.box().call()
                const { REACT_APP_HAPPYBOXABI } = process.env;
                const boxx = new web3.eth.Contract(JSON.parse(REACT_APP_HAPPYBOXABI), box) 
                t = await boxx.methods.master().call()
                try{document.getElementById("Shop_social").innerHTML=utils.getAvatar(t)}catch(e){}  
            })
            this.setState({wallet:t})
            this.render()          
        }
    }

    async avatar(){
        window.history.pushState('avatar_page', 'AVATAR PAGE', '/happyfarm?account='+this.state.wallet)
        this.props.update("EmptyAvatar")
    }

    async showTicket(addr,ind){
        await this.setState({ticket:addr,ticketindex:ind,freebies:"",freebiesindex:0,swapper:"",swapperindex:0})
        console.log("SHOW TICKET "+addr,ind)
        this.render()
    }

    async showFreebie(addr,ind){
        await this.setState({ticket:"",ticketindex:0,freebies:addr,freebiesindex:ind,swapper:"",swapperindex:0})
        console.log("SHOW FREEBIE "+addr,ind)
        this.render()
    }

    async showSwapper(addr,ind){
        await this.setState({ticket:"",ticketindex:0,freebies:"",freebiesindex:0,swapper:addr,swapperindex:ind})
        console.log("SHOW SWAPPER "+addr,ind)
        this.render()
    }

    async setAmountItems(i){
        try{document.getElementById("Shop_amount").innerHTML="Items: "+i+" 🏺"}catch(e){}
        this.setState({amountItems:i})
    }


    editMode(){
        (!this.state.edit)?this.setState({edit:true}):this.setState({edit:false})
    }

    setMode(md){
        this.setState({mode:md})
        console.log("MODE",md)
    }

    async loadHeader(){
            let addr=""
            let shopNam=""
            let shopDescriptio=""
            await window.ethereum.enable().then(async function (accounts) {
                let metafound=false
                let mtdt
                const { REACT_APP_METAABI } = await process.env;
                const { REACT_APP_META } = await  process.env;
                if(utils.getUrlVars()["shop"]){
                    addr=utils.getUrlVars()["shop"]
                    const metadata = new web3.eth.Contract(JSON.parse(REACT_APP_METAABI), REACT_APP_META) 
                    mtdt=await metadata.methods.meta(addr).call()
                    if(mtdt.length>=0)metafound=true
                    console.log("MTDT>>>>Z> "+addr+" "+REACT_APP_META,mtdt)
                }
                if((!utils.getUrlVars()["shop"])||(!metafound)){
                    addr=accounts[0]
                    const metadata = new web3.eth.Contract(JSON.parse(REACT_APP_METAABI), REACT_APP_META) 
                    mtdt=await metadata.methods.meta(addr).call()
                }
                document.getElementById("Shop_twitter_pan").style.display='none'
                document.getElementById("Shop_discord_pan").style.display='none'       
                if(mtdt.length>0){
                    console.log("MTDT",mtdt)
                    await fetch(utils.formatURL(mtdt)).then(response => response.json()).then(
                    response => {
                        console.log(JSON.stringify(response))
                        shopNam=response[0]["name"]  
                        shopDescriptio=response[0]["description"]
                        document.getElementById("Shop_header_mainlabel").innerHTML=shopNam
                        document.getElementById("Shop_header_description").innerHTML=shopDescriptio
                        let back=response[0]["image"]
                        if(back=="")back='https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/27592dd5-bdf2-4e90-9e17-852077de3dd0/d2aytcz-fd1a408a-541d-465d-9f8d-9c662b5ba6db.jpg/v1/fill/w_900,h_600,q_75,strp/fading_blue_grunge_by_dyingbeautystock.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..JSbyhtv1aQd2O8AzG2x1UTZLfyNdtwGNtNSgzKCDFTE'
                        document.getElementById("Shop_header_image").style.backgroundImage='url('+back+')'           
                        if(response[0]["twitter"].length>0){
                            document.getElementById("Shop_twitter_pan").addEventListener("click", () => utils.jumpsocial("https://twitter.com/"+response[0]["twitter"]))
                            document.getElementById("Shop_twitter_pan").style.display='visible'                     
                        }
                        if(response[0]["discord"].length>0){
                            document.getElementById("Shop_discord_pan").addEventListener("click",  () => utils.jumpsocial("https://discord.gg/"+response[0]["discord"]))
                            document.getElementById("Shop_discord_pan").style.display='visible'
                        }       
                    })
                }
            })
            await this.setState({shopName:shopNam,shopDescription:shopDescriptio})                   
        }

        setCookie(cname, cvalue,id) {
            let s=this.getCookie(cname)
            let arr=s.split(",")
            let arr2=[]
            let t=false
            for(let i=0;i<arr.length;i++){               
                if(arr[i]==cvalue+"_"+id){
                    t=true 
                    document.getElementById("Shop_settings_favoriteShop").style.opacity=0.3              
                }else{
                    arr2.push(arr[i])
                    document.getElementById("Shop_settings_favoriteShop").style.opacity=1
                }
            } 
            if(!t){
                arr2.push(cvalue+"_"+id)   
            }
            document.cookie = cname + "=" + arr2.toString() + "; path=/"        
        }
        
        getCookie(cname) {
            var name = cname + "=";
            var decodedCookie = decodeURIComponent(document.cookie);
            var ca = decodedCookie.split(';');
            for(var i = 0; i <ca.length; i++) {
              var c = ca[i];
              while (c.charAt(0) == ' ') {
                c = c.substring(1);
              }
              if (c.indexOf(name) == 0) {
                return c.substring(name.length, c.length);
              }
            }
            return "";
          }
    

    render(){
        let s=this.getCookie("favoriteShop")
        let arr=s.split(",")
        let favoriteShop=false
        for(let i=0;i<arr.length;i++){
            if(arr[i]==utils.getUrlVars()["shop"]+"_0")
            favoriteShop=true 
        }

const final = [];
let n=0;
if(utils.getUrlVars()["shop"]>""){
    console.log("RENDERING SHOP")
return(
        <div id="Shop_panel">
            <div id="Shop_header">
                <div id="Shop_header_subpanel">
                    <div id="Shop_header_image">
                        <div id="Shop_header_mainlabel" className="rainbow"><span id="stakecontract" aria-labelledby="jsx-a11y/accessible-emoji" role="img" onClick={() => utils.jump()}>🌈 </span> SmartShop</div>   
                    </div>
                    <div id="Shop_header_description"></div>
                </div>
            </div>
                <div id="Shop_items_container">
                    <div className="Shop_row2">
                        <div className="Shop_label">
                            {(this.state.ismaster)?
                            <span id="Shop_settings" className="" onClick={() => this.editMode()}>⚙️</span>
                            :
                            <span id="Shop_settings_favoriteShop" className={(favoriteShop)?"":"disabled"}  onClick={() => this.setCookie("favoriteShop",this.state.ticket,0)}>❤️</span>
                            }
                            <div id="Shop_twitter_pan" className="Shop_twitter" ><img id="Shop_twitter" className="Shop_twitter" src="https://i.imgur.com/6I12WrS.png"></img></div>
                            <div id="Shop_discord_pan" className="Shop_discord"><img id="Shop_discord" className="Shop_discord" src="https://i.imgur.com/Owul43B.png"></img></div>
                            {(!this.state.edit)?
                            <div id="Shop_amount" className="right" tot={this.state.amountItems}>Items: {this.state.amountItems} 🏺</div>
                            :
                            <>  
                            <span id="Shop_social" className="right" onClick={() => this.avatar()}>{utils.getAvatar(this.state.wallet)}</span>
                            </>
                            }
                        </div>            
                    </div>
                    <div className="clear"/>    
                </div>
                {(this.state.edit)?<SingleShopMenu setMode={this.setMode}></SingleShopMenu>:<></>}
                {
                            (this.state.mode=="default")?
                            <>
                            {(this.state.ticket!="")?<PRiceList1_0  db={this.state.box} edit={this.state.edit} address={utils.getUrlVars()["shop"]} index={0} setAmount={this.setAmountItems}></PRiceList1_0>:<></>}
                            {(this.state.freebies!="")?<FreebiesList1_0 db={this.state.box} edit={this.state.edit} address={utils.getUrlVars()["shop"]} index={2} setAmount={this.setAmountItems}></FreebiesList1_0>:<></>}
                            {(this.state.swapper!="")?<PairPriceList1_0 db={this.state.box} edit={this.state.edit} address={utils.getUrlVars()["shop"]} index={2} setAmount={this.setAmountItems}></PairPriceList1_0>:<></>}
                            
                            {(this.state.edit)?<ShopMeta></ShopMeta>:<></>}
                            </>
                        :
                            <>
                            <center><img id="menu_instructions_image" src="https://i.imgur.com/6ygtcQI.png"></img></center>
                            </>
                        }
                <div id="footer">
                {(!this.state.sharing)?
                   <div title="Tweet" onClick={() => this.generateSharePage(60) }><img alt="Tweet" className="socialShare" src="https://i.imgur.com/cHP6bia.png" /></div>
                  :<>
                  <div id="twitter_sharing_info_panel" className="twitter_sharing_info_panel">Please wait while propagating twitter card on ipfs...</div>
                    <input id="count_done" type="button" value="🤹 TRY AGAIN!" onClick={() => this.generateSharePage(120) }></input>
                </>}
                        </div>
            </div>
     
);
}else{
    console.log("RENDERING WALLET SHOP",this.state.ticket)
  
        return ( 
        <div id="Shop_panel">
            <div id="Shop_header">
            <div id="Shop_header_subpanel">
            <div id="Shop_header_image">
            <div id="Shop_header_mainlabel" className="rainbow"><span id="stakecontract" aria-labelledby="jsx-a11y/accessible-emoji" role="img" onClick={() => utils.jump()}>🌈 </span> SmartShop</div>
            
            </div>
            <div id="Shop_header_description"></div>
            </div>
            </div>
                <div id="Shop_items_container">
                    <div className="Shop_row2">
                        <div className="Shop_label"><span id="Shop_settings" className="" onClick={() => this.editMode()}>⚙️</span>
                             
        <div id="Shop_social" className="right" onClick={() => this.avatar()}>{utils.getAvatar(this.state.wallet)}</div>
        <div id="Shop_twitter_pan" className="Shop_twitter" ><img id="Shop_twitter" className="Shop_twitter" src="https://i.imgur.com/6I12WrS.png"></img></div>
        <div id="Shop_discord_pan" className="Shop_discord"><img id="Shop_discord" className="Shop_discord" src="https://i.imgur.com/Owul43B.png"></img></div>
                        
                        </div>            
                    </div>
                    <div className="clear"/>
                  
                    
                </div>
                
               
                    <>
                    <ShopMenu setMode={this.setMode}></ShopMenu>
                    {(this.state.mode=="default")?
                        <>
                        <BoxModules showBox={this.showBox}></BoxModules>           
                        <ShopModulesPanel db={this.state.box} boxindex={this.state.boxindex} ticket={this.showTicket} freebie={this.showFreebie} swapper={this.showSwapper}></ShopModulesPanel>
                        {(this.state.ticket!="")?
                            <PRiceList1_0 db={this.state.box} edit={this.state.edit} address={this.state.ticket} index={this.state.ticketindex}></PRiceList1_0>
                        :<></>
                        }
                        {(this.state.freebies!="")?
                            <FreebiesList1_0 db={this.state.box} edit={this.state.edit} address={this.state.freebies} index={this.state.freebiesindex}></FreebiesList1_0>
                        :<></>
                        }
                        {(this.state.swapper!="")?
                            <PairPriceList1_0 db={this.state.box} edit={this.state.edit} address={this.state.swapper} index={this.state.swapperindex}></PairPriceList1_0>
                        :<></>
                        }
                        </>
                    :
                        <>{
                            (this.state.mode=="meta")?
                            <ShopMeta address={this.s}></ShopMeta>
                        :
                        <>{
                            (this.state.mode=="userbook")?
                            <SingleUserBook showBox={this.showBox} edit={this.state.edit}></SingleUserBook>
                        :
                            <>
                            <center><img id="menu_instructions_image" src="https://i.imgur.com/6ygtcQI.png"></img></center>
                            </>
                        }</>
                        }</>
                    }
                    </>
               
                   <div id="footer>">
                   </div>
          </div>
        );

        }
    }


}


const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")

export default Prices